.card {
  display: grid;
  padding: 19px 0 25px 0;

  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(3, auto);
  row-gap: 13px;
}

.card--last-child {
  grid-template-columns: 1fr;

  .card__link {
    grid-row: 1;
  }

  .card__title {
    grid-column: 1;
  }

  .card__order-button {
    grid-row: 3;
  }
}

.card__link {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0;

  grid-row: 1 / 3;
}

.card__image {
  display: block;
}

.card__title {
  max-width: 76px;
  margin: 0;

  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: $color-nero;
  text-transform: uppercase;

  grid-column: 2;
  grid-row: 1;
}

.card__title-link {
  color: $color-nero;
}

.card__features-list {
  font-size: 12px;
  line-height: 16px;
  font-family: $font-lato;
  color: $color-charcoal;

  grid-column: 2;
  grid-row: 2;
}

.card__order-button {
  padding: 10px;

  font-size: 16px;
  line-height: 20px;
  font-family: $font-oswald;
  text-align: center;

  grid-column: 1 / -1;
  grid-row: 4;
}
